import React, { Component } from 'react';
import { Card, CardTitle, CardText, CardActions } from 'react-mdl';

class ProjectTile extends Component {
  render() {
    return (
        <Card shadow={0} style={{width: '320px', height: '320px', margin: 'auto'}}>
            <CardTitle expand style={{color: '#fff', background: this.props.projectLogoTool, backgroundSize: this.props.projectLogoSize}}>{this.props.projectTitle}</CardTitle>
            <CardText>
                {this.props.projDescription}
            </CardText>
            <CardActions border>
                <a href={this.props.projectURL} target="_blank" rel="noopener noreferrer">{this.props.learnMore}</a>
            </CardActions>
        </Card>
    );
  }
}

export default ProjectTile;
