import React, { Component } from 'react';
import { Grid,Cell } from 'react-mdl';

class Experience extends Component {
  render() {
    return (
    	<Grid>
    		<Cell col={4}>
    			<p style={{borderTop:'8px solid transparent'}}>{this.props.startYear} - {this.props.endYear}</p>
    		</Cell>
    		<Cell col={6}>
    			<h4 style={{marginTop:'0'}}>{this.props.compName}</h4>
    			<p>{this.props.roleDescription}</p>
    		</Cell>
    	</Grid>
    );
  }
}

export default Experience;
